.card-4{
    width: 100%;
    height: 80vh;
    display: flex;
    margin-top: -5%;
}
.b-div,.s-div{
    width: 50%;
    display: flex;
    justify-content: space-around;
}
.b-div{
    display: flex;
    justify-content: center;
}
.b-content{
    background-image: url('../assets/Images/b-div-img.png');
    background-position: center right;
    background-size: 100%;
    height: 70%;
    width: 80%;
    margin-left: 10%;
    margin-top: 50%;
    transform: translate(0,-60%);
}
.b-content h1,.b-content h3{
    position: absolute;
    top: 2rem;
    left: 2rem;
    color: green;
}
.b-content h1{
    text-align: left;
    top: 4rem;
}
.s-div{
    flex-direction: column;
    justify-content: center;
    gap: 3rem;
}
.s1,.s2{
    position: relative;
    background-image: url('../assets/Images/s1-img.png');
    height: 30%;
    background-position: center right;
    background-size: 100%; 
}
.s1{
    margin-top: 15%;
}
.s2{
    background-image: url('../assets/Images/s2.png');
}
.s1 h1,.s1 h3,.s2 h1,.s2 h3{
    position: absolute;
    top: 2rem;
    left: 2rem;
    color: green;
}
.s1 h3,.s2 h3{
    top: 1rem;
}
.s1 h1,.s2 h1{
    text-align: left;
    top: 4.5rem;
}
@media screen and (max-width: 580px) {
    .card-4{
        flex-direction: column;
        gap: 3rem;
        height: 100vh;
    }
    .b-div{
        width: 80%;
        margin-left: 10%;
        margin-top: -12%;
    }
    .b-content{
        width: 100%;
        margin-left: 0%;
        height: 100%;
        margin-bottom: 32%;
        margin-top: 10;
    }
    .s-div{
        width: 100%;
        height: 50%;
        gap: 1rem;
        margin-top: -10%;
    }
    .s1,.s2{
        width: 80%;
        height: 60%;
        background-size: 137%;
        margin-left: 10%;
    }
    .s1 h1,.s2 h1{
        top: 3rem;
    }
    .s1 h3,.s2 h3{
        top: 1rem;

    }
}