.card{
    height: 100vh;
    width: 100%;
    max-height: 835px;
    background-color: yellow;
    background-image: url('../assets/Images/hero-background-img.png');
    background-repeat: no-repeat;
    background-position: 115% 0%;  
}
.leaf-1{
    position: absolute;
    top: 30%;
    left: 3rem;
    width: 3.5rem;
}
.leaf-2{
    position: absolute;
    bottom: -4%;
    width: 4rem;
    right: 4%;
}
.tomato{
    position: absolute;
    bottom: 1rem;
    left: 57%;
    width: 4.5rem;
}
.hero-images{
    display: flex;
    justify-content: space-around;
}
.hero-text>img{
    width: 70%;
    margin-left: 6rem;
    margin-top: 1rem;
}
.hero-plate>img{
    width: 80%;
}
@media screen and (max-width: 580px) {
    .card{
        background-position: -50% 0;
        
    }
    .leaf-1{
        width: 2rem;
        top: 25%;
    }
    .leaf-2{
        width: 2.5rem;
        bottom: -2%;
    }
    .tomato{
        width: 3rem;
        left: 20%;
        bottom: 0.5rem;
    }
    .hero-images{
        flex-direction: column;
    }
    .hero-text>img{
        width: 60%;
        margin-left: 1.5rem;
    }
    .hero-plate>img{
        width: 70%;
        margin-top: 3rme;
    
    }
}