.card-6{
    width: 100%;
    height: 85vh;
    margin-top: -30vh;
    background-image: url('../assets/Images/footer-background.png');
    background-size: 100%;
    background-position: top top;
    display: flex;
    position: relative;
    
}
.w-logo{
    /* border: 1px solid red; */
    margin-top: 2rem;
    margin-left: 2rem;
    width: 20%;
}
.about-company{
    width: 55%;
    margin-left: 5%;
    margin-right: 10%;
    margin-top: 2rem;
    color: white;
    text-align: left;
    word-spacing: 2px;
    line-height: 1.5rem;

}
.con-soc{
    display: flex;
    flex-direction: column;
    margin-top: 10%;
    height: 20%;
    width: 80%;
    max-lines: 15%;

}
.location,.email{
    display: flex;
    color: white;
    margin-top: 1rem;
    gap: 1rem;
}
.location img{
        width: 1rem;
        height: 1rem;
        margin-top: 1rem;
}
.email img{
    width: 1rem;
    height: 1rem;
    margin-top: 1rem;
}
.con-soc p{
    margin-top: 1rem;
    word-spacing: 1px;
}
.twitter,.instagram,.facebook,.whatsapp{
    color: white;
    font-size: xx-large;
}
.facebook,.whatsapp{
    font-size: xx-large;
    padding-top: .2rem;

}
.facebook:hover,.twitter:hover,.instagram:hover,.whatsapp:hover{
    color: yellow;
}
.social-media{
    width: 15%;
    position: absolute;
    right: 2rem;
    bottom: 2rem;
    padding: 1rem;
    display: flex;
    flex-direction: row;
    gap: 1rem;
    
}
@media screen and (max-width: 580px) {
    .card-6{
        flex-direction: column;
        background-repeat: no-repeat;
        background-position: bottom bottom;
        background-size: contain;
        background-size:     contain;
        background-repeat:   no-repeat;
        background-position: bottom center;
        height: 110vh;
        background-color: black;
    }
    .w-logo{
        margin-left: 50%;
        transform: translate(-100%,0);
    }
    .about-company{
        text-align: center;
        margin: 0 auto;
        margin-top: 1rem;
        width: 70%;
    }
    .con-soc{
        margin: 0 auto;
        margin-left: 50%;
        transform: translate(-45%,0);
    }
    .location p{
        margin-left: -.5rem;
    }
    .social-media{
        width: 80%;
        bottom: -35%;
        right: 15%;
        
    }

}

