/* Navbar styling */
.navbar{
    display: flex;
    justify-content: space-between;
    background-color: transparent;
    width: 100%; 
}
.logo img{
    margin-left: .5rem;
    /* margin-top: 1rem; */
    padding: 1rem;
    width: 10rem;
    margin-top: 1rem;
}
.navbar ul{
    display: flex;
    margin-right: 7rem;
    margin-top: 5.5rem;
    font-weight: 550;
    display: flex;
}
.navbar ul a li{
    list-style:  none;
    text-decoration: none;
    margin-right: 2rem;
    font-size: 1rem;
    color: black;
    transition: .5s;
    font-family: monospace; 
}
#home:hover,#menu:hover{
    color: green;
    font-weight: 600;
}
.navbar ul a li:hover{
    color: white;
}
#hamburger{
    color: yellow;
    margin-right: 1rem;
    margin-top: 3.2rem;
    font-size: 1.9rem;
    display: none;
}
#hamburger:hover{
    color: white;
}
/* End of Navbar styling */
/* Media Query */
@media screen and (max-width: 580px) {
    
    #hamburger{
        display: initial;
        align-items: center;
        z-index: 2;
    }
    #hamburger:hover{
        color: white;
        transition: .5s;
    }
    .navbar ul{
        position: absolute;
        top: 50%;
        width: 100%;
        margin-top: 0px;
        margin-top: -1px;
        height: 100%;
        transform: translate(-100%,-50%);
        background-color: transparent;
        flex-direction: column;
        gap: 1rem;
        display: none;
        background-color: rgba(0, 0, 0, 0.945);
        z-index: 1;
        color: yellow;
    }
    .navbar ul a li{
        margin-top: 4rem;
        margin-right: 0;
        font-size: 2rem;
    }
    .navbar ul a li:hover{
        color: white;
    }
    .navbar ul a li:nth-child(0){
        margin-top: 3rem;
    }
    .logo img{
        margin-top: 1.5rem;
        width: 7rem;
    }
    #home{
        margin-top: 8rem;
    }
    #home:hover,#menu:hover{
        color: white;
    }
    .navbar ul a li{
        color: yellow;
    }
}
/* End of Media Query */