.card-3{
    width: 100%;
    height: 100vh;
}
.card-3 h4{
    text-align: center;
    margin-top: 3rem;
    color: black;
}
.upcoming-events{
    width: 20%;
    margin-top: 1rem;
}
.event-desc{
    margin: 0 auto;
    margin-top: 1rem;
    padding: 0 5rem;
    font-size: 1rem;
    word-spacing: 2px;
    line-height: 1.5rem;
    justify-content: center;
    text-align: center;
}
.cards{
    display: flex;
    width: 100%;
    margin-left: 0;
    justify-content: flex-start;
}
.card1,.card2,.card3{ 
    width: 30%;
    height: 250px;
    margin-top: 8rem;
    overflow: hidden;
}
.card1,.card3{
    background-color: black;
}
.card1 img,.card3 img{
    opacity: .2;
}
.card2{
    z-index: 2;
    width: 40%;
    margin-top: 3rem;
    height: 100%;
    overflow: initial;
}
.card1 img,.card2 img,.card3 img{
    width: 100%;
    margin-left: 0;
}
.card2 img{
    margin-left: -10%;
    margin-top: -1rem;
    width: 135%;
}
.prev-item,.next-item{
    display: none;
}
.navigation-circle{
    width: 10%;
    margin: 0 auto;
    margin-top: -1.5rem;
    display: flex;
    justify-content: space-around;    
}
.circle-1,.circle-2,.circle-3{
    width: .8rem;
    height: .8rem;
    background-color: rgb(255, 255, 255);
    border-radius: 50%;
    border: 1px solid green;
}
.circle-2{
    background-color: yellow;   
}
@media screen and (max-width: 580px) {
    .card-3{
        margin-top: 140vh;
    }
    .card-3 h4{
        margin-top: 2rem;
    }
    .upcoming-events{
        width: 60%;
    }
    .card1,.card2,.card3{ 
        display: none;
    }
    .card2{
        display: initial;
        width: 65%;
        margin-left: 15%;
    }
    .card2 img{
        margin-top: 0;
    }
    .prev-item,.next-item{
        display: block;
        position: absolute;
        width: 3rem;
        height: 3rem;
        background-color: green;
        color: yellow;
        border: 2px solid yellow;
        border-radius: 50%;
        margin: o auto;
        font-size: 1.4rem;
        padding: .6rem 0;
        font-weight: bolder;
        left: 1rem;
        top: 399vh;
        z-index: 2;
    }
    .next-item{
        left: 82%;
        top: 399vh;
    }
    .navigation-circle{
        width: 20%;
        margin-top: .3rem;
    }
}