.sampleCard{
    width: 100%;
    height: 100vh;
}
.choose-enjoy{
    margin-top: 3rem;
    width: 25%;
}
.choose-text{
    margin: 0 auto;
    margin-top: 1rem;
    padding: 0 5rem;
    font-size: 1rem;
    word-spacing: 2px;
    line-height: 1.5rem;
    justify-content: center;
    text-align: center;
}

.items{
    display: flex;
    justify-content: space-around;
}
.item img{
    width: 60%;
    margin-top: 3rem;
}
.item .item-title{
    font-size: 1rem;
    margin-top: 1rem;
    font-weight: 600;
}
.item .item-desc{
    margin-top: .5rem;
    font-size: .9rem;
    font-weight: 300;
}
.item .item-button{
    margin-top: 2.5rem;
    width: 30%;
    height: 2rem;
    border: none;
    border-bottom: 3px solid green;
    background-color: yellow;
    font-weight: 600;
    transition: .5s;
}
.item .item-button:hover{
    background-color: rgb(68, 65, 65);
    color: white;
}
.background,.item2,.item3{
    position: absolute;
    left: 0;
    top: 143%;
    display: flex;
    width: 100%;
    height: 8rem;
    position: absolute;
    background-color: #e8e6a9;
    border-bottom: 1rem solid #3f6602;
    z-index: -1;
}
.item-background{
    width: 0%;
    height: 8rem;
}




@media screen and (max-width: 580px) {

    .choose-enjoy{
        width: 50%;
        margin-top: 1.5rem;
    }
    .choose-text{
        padding: 0 1.5rem;
        line-height: 1.2rem;
    }
    .items{
        flex-direction: column;
    }
    .item img{
        width: 60%;
        margin-top: 4rem;
    }
    .item .item-title{
        font-weight: bolder;
    }
    .item .item-desc{
        font-size: 1rem;
        padding: 0 1rem;
    }
    .item .item-button{
        width: 40%;
        height: 2.5rem;
        font-weight: bolder;
    }
    .background{
        flex-direction: column;
        margin-top: -1.5rem;
        top: 148%;
    }
    .item2,.item3{
        width: 100%;
    }
    .item2{
        top: 410%;
    }
    .item3{
        top: 810%;
    }


}