.card-5{
    width: 100%;
    height: 100vh;
}
.c5-heading{
    margin-top: 2rem;
}
.c5-subhead-img{
    margin-top: 1rem;
}
.contactForm{
    display: flex;
    flex-direction: row;
    height: 50%;
    margin-top: 0rem;
    margin-right: 2rem;
}
.contactForm div{
    display: flex;
    flex-direction: column;
}
.f1{
    margin-left: 50%;
}
.f2,.f1{
    transform: translate(-100%,0);
    margin-top: 2rem;
}
.f1 input,.f2 input,.input{
    margin-top: 2rem;
    margin-left: 2rem;
    width: 18rem;
    height: 3rem;
    border-radius: 4px;
    border: 1px solid rgb(180, 173, 173);
    padding: 1rem;
}
.input{
    font-weight: 700;
    font-size: 1.2rem;
    text-align: center;
    text-transform: uppercase;
    background-color: yellow;
    border: none;
}
@media screen and (max-width: 580px) {
    .card-5{
        height: 120vh;
    }
    .contactForm{
        flex-direction: column;
    }
    .c5-heading{
        margin-top: -4rem;
    }
    .f2{

        margin: 0;
        position: relative;
    }
    .c5-subhead-img{
        width: 80%;
        margin-left: 5%;
    }
    .f2,.f1{
        transform: translate(0,0);
        margin-top: 2rem;
        margin-left: .5rem;
    }
    .f2{
        margin-top: 0;
    }
    .contactForm{
        margin-top: -1rem;
    }
}